import React, { useEffect } from "react";

import "./Portfolio.css"

import Navbar from "../../components/genral_components/Navbar/Navbar"

import PortfolioBanner from '../../components/portfolio-components/portfolio-banner/PortfolioBanner'

import PortfolioWorks from '../../components/portfolio-components/portfolio-works/PortfolioWorks'

import Carousel from "../../components/home_page_components/carousel/Carousel"

import Footer from "../../components/genral_components/Footer/Footer"

import { useLocation } from "react-router-dom"

export default function Portfolio() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className='portfolio-main-container'>
      <Navbar/>

      <PortfolioBanner/>

      <PortfolioWorks/>

      <Carousel/>

      <br/>
      <br/>
      <br/>
      
      <Footer/>
    </div>
  )
}
