import React, { useRef, useEffect, useState } from "react";

import "./PortfolioCardsRow.css";

import picMissing from "./picNotFound.jpg";

import { RiZoomInLine } from "react-icons/ri";

import PortfolioModalTwo from "../portfolio-modal-two/PortfolioModalTwo";

export default function PortfolioCardsRow(prop) {
  
  const divCount = prop.numberOfCards;

  const modalRef = useRef(null);

  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    const handleClickOutsideModal = (event) => {
      if (modalRef.current && !modalRef.current.contains(event.target)) {
        // Click occurred outside the modal div, close the modal
        setShowModal(false);
      }
    };

    // Attach the event listener when the component mounts
    document.addEventListener("mousedown", handleClickOutsideModal);

    // Clean up the event listener when the component unmounts
    return () => {
      document.removeEventListener("mousedown", handleClickOutsideModal);
    };
  }, []);

  const [imageForModal, setImageForModal] = useState(null);

  const [imagesToShow, setImagesToShow] = useState(prop.initialImagesToShow);

  const [screenWidth, setScreenWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => {
      setScreenWidth(window.innerWidth);
    };

    // Add event listener for window resize
    window.addEventListener('resize', handleResize);

    // Clean up the event listener when the component unmounts
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <>
      <div className="portfolio-cards-row">
        {Array(divCount)
          .fill()
          .map(
            (_, index) =>
              index < imagesToShow && (
                <div className="portfolio-cards-row-card-one" key={index}>
                  <img
                    src={
                      prop.pictures[index] ? prop.pictures[index] : picMissing
                    }
                    alt="First work"
                  />

                  {(screenWidth > 767) && <div
                    className="portfolio-cards-row-card-one-div-two"
                    onClick={() => {
                      setImageForModal(index);
                      setShowModal(!showModal);
                    }}
                  >
                    <RiZoomInLine color="#fff" size={40} />
                  </div>}
                </div>
              )
          )}

        {(screenWidth > 767 && showModal) && (
          <div className="modal-div" ref={modalRef}>
            <PortfolioModalTwo
              picture={
                prop.pictures[imageForModal]
                  ? prop.pictures[imageForModal]
                  : picMissing
              }
            />
          </div>
        )}
      </div>

      <div className="load-more-button-div">
        <button
          onClick={() => {
            if (prop.pictures.length > imagesToShow) {
              const imagesToAdd = prop.pictures.length - imagesToShow;

              setImagesToShow(imagesToShow + imagesToAdd);
            }
          }}
          className="load-more-button"
        >
          Load More!
        </button>
      </div>
    </>
  );
}
